import React from 'react'
import { useState } from 'react'
import ss from '.././images/ss.png'
import axios from 'axios'
import { tz } from './apis'
import { useEffect } from 'react'
const Siteuserlogin = () => {
    const [email, setemail] = useState('')
    const [pass, setpass] = useState('')
    useEffect(() => {
        var uu=localStorage.getItem('siteuserapi')
        var uu2=localStorage.getItem('siteuserid')
        if(uu&&uu2&&uu2.length>2&&uu==='^%$234'){
            
          window.location.pathname='siteuser'
        }
        else{
        }
                return () => {
        
                }
            }, [])
            
    function login() {
        axios.post(`${tz}/siteuser/findbyname`,{
            name:email,
        }).then((res)=>{
            console.log(res)
            if(res.data==='error'){
                alert('Invalid Credentials')
            }
            else{
                if(pass==='123456'){
                    
                localStorage.setItem('siteuserid',res.data.Siteuserd[0]._id)
                localStorage.setItem('siteuserapi','^%$234')
                window.location.pathname='siteuser'
                }
                else{
                    alert('Type Correct credentials')
              
            }
        }
          
        })
        
    }
  return (
    <div className="login">
        <div className="sublogin">
            <img src={ss} alt="" />
            <h1>WELCOME</h1>
            <h2>
                Name
            </h2>
            <input onChange={e=>setemail(e.target.value)} type="text"  />
            <h2>Password</h2>
            <input type="password"  onChange={e=>setpass(e.target.value)} />
<button onClick={e=>login()}>Login</button>

        </div>
    </div>
  )
}

export default Siteuserlogin